export default {
	data() {
		return {
			flag: true,
			hd:false
		};
	},
	methods: {
		changeColor: function(flag) {
			this.flag = flag;
		},
		hiddenNav:function(hd){
			this.hd=hd;
		},
		goback: function() {
			uni.navigateBack({
				delta: 1
			});
		}
	}
};
